import { v4 as uuidv4 } from "uuid";

import {
  WS_CONNECTION_SUCCESS,
  WS_CONNECTION_ERROR,
  WS_CONNECTION_CLOSED,
  WS_GET_ORDERS,
} from "../actions/ws-actions";

const initialState = {
  wsConnected: false,
  error: undefined,
  isRefreshed: false,
  allOrders: {
    orders: [],
    total: 0,
    totalToday: 0,
  },
};

const wsReducer = (state = initialState, { type, payload, uuid }) => {
  switch (type) {
    case WS_CONNECTION_SUCCESS:
      return {
        ...state,
        error: undefined,
        wsConnected: true,
        isRefreshed: true,
      };
    case WS_CONNECTION_ERROR:
      return {
        ...state,
        error: payload,
        wsConnected: false,
      };

    //      WS_CONNECTION_CLOSED,   
    //   wsConnected   false
    case WS_CONNECTION_CLOSED:
      return {
        ...state,
        error: undefined,
        wsConnected: false,
        isRefreshed: false,
      };

    //      WS_GET_ORDERS
    //  ,     
    //  messages  ,    
    case WS_GET_ORDERS:
      return {
        ...state,
        error: undefined,
        allOrders: {orders: payload.data.orders, total: payload.data.total, totalToday: payload.data.totalToday}
      };

    default:
      return state;
  }
};

export default wsReducer;
